<template>
    <div>

        <button @click="playChess" class="button">‚ôüÔ∏è</button>

        <div class="chessAcceptWindow" v-if="showChessAcceptWindow">
            <!-- user "{{ curUser.fakeName }}" invite you to play chess. 
                <el-button type="danger" round @click="closeWindow">acccept</el-button> -->
            <h3>Game</h3>
            <el-form ref="ruleFormRef" :rules="rules" :model="ruleForm" label-width="auto" style="max-width: 600px;">
                <el-form-item label="RoomID" prop="roomID">
                    <el-input v-model="ruleForm.roomID" />
                </el-form-item>
                <el-form-item label="Ê∏∏ÊàèÁ±ªÂûã" prop="type">
                    <el-input v-model="ruleForm.type" />
                </el-form-item>

                <div style="display: flex; gap: 10px; position: relative; left: 15%;">
                    <router-link target="_blank" :to="generateLink('create')">
                        <el-button type="warning" @click="submitFormCreate($event, ruleFormRef)">ÂàõÂª∫</el-button>
                    </router-link>
                    <router-link target="_blank" :to="generateLink('create')">
                        <el-button type="primary" @click="submitFormLogin($event, ruleFormRef)">Âä†ÂÖ•</el-button>
                    </router-link>
                    <el-button @click.prevent="closeWindow">ÂèñÊ∂à</el-button>
                </div>

            </el-form>
        </div>

    </div>
</template>

<script>
import { ref, inject, computed } from 'vue';

export default {
    setup() {
        const loginInfo = inject('login-info');

        // ÂæÖÂä†ÂÖ•‰∏çÁôªÂΩï‰∏çÂºÄÂêØÊ£ãÂ±ÄËÆæÁΩÆ
        const curUser = computed(() => {
            // console.log(loginInfo.value.userId);
            return loginInfo.value ? loginInfo.value : '';
        });

        const ruleFormRef = ref(null);

        const ruleForm = ref({
            roomID: '',
            type: '',
        });

        const rules = {
            roomID: [{ required: true, message: 'ËØ∑ËæìÂÖ•ID', trigger: 'blur' }],
            type: [{ required: true, message: 'ËØ∑ËæìÂÖ•ÂÅáÂêç', trigger: 'blur' }]
        };

        return {
            curUser,
            ruleFormRef,
            ruleForm,
            rules,
            loginInfo
        }
    },
    data() {
        return {
            showChessAcceptWindow: false,
        };
    },
    methods: {
        generateLink(action) {
        return {
            name: 'ChessGame',
            query: {
            roomID: this.ruleForm.roomID,
            type: this.ruleForm.type,
            action: action,
            JWT: this.loginInfo.JWT,
            playerID: this.loginInfo.userId
            }
        };
        },
        playChess() {
            this.$emit('start-chess', 'chess');
            this.showChessAcceptWindow = true;
            this.broadcastRequest();
        },
        broadcastRequest() {
            // Ê£ãÂ±ÄÂ∑≤ÁªèÂºÄÂßãÔºåÂπøÊí≠ËØ•Ê∂àÊÅØÂà∞Âêå‰∏ÄËÅäÂ§©ÂÆ§ÂÖ∂‰ªñÊâÄÊúâÁé©ÂÆ∂ËÅäÂ§©Ê°Ü‰∏≠
            // ÂÖ∑‰ΩìÊù•ËØ¥Â∞±ÊòØÂ∞Ü‰ªñ‰ª¨ÁöÑshowChessAcceptWindowËÆæÁΩÆ‰∏∫trueÔºåÈÇÄËØ∑‰ªñ‰ª¨ËøõÊù•

            // ËøôÈáåÂ∫îËØ•Ë¶ÅÂëäËØâÂêéÁ´ØÊúâÂì™‰∫õÁî®Êà∑ËøõÂÖ•‰∫ÜÊ£ãÂ±Ä
            // Â¶ÇÊûúÊ£ÄÊµãÂà∞ÊàøÈó¥ÂÜÖ‰∫∫Êï∞=2ÔºåÈÇ£‰πàÂ∞ÜshowChessAcceptWindowËÆæÁΩÆ‰∏∫falseÔºåÂêåÊó∂ÂºÄÂßãÊ∏∏Êàè
        },
        closeWindow() {
            // ËøôÈáåËøòË¶ÅÈÄöÁü•ÂêéÁ´ØÔºåÂØπÊñπÂ∑≤ÁªèÊé•Âèó‰∫ÜÈÇÄËØ∑
            this.showChessAcceptWindow = false;
        },
        submitFormCreate(event, formEl) {
            if (!formEl) return
            formEl.validate((valid) => {
                if (valid) {
                    this.$emit('create-game', this.ruleForm.roomID, this.ruleForm.type);
                    this.closeWindow();
                } else {
                    event.preventDefault();
                    alert('create failed!');
                }
            })
        },
        submitFormLogin(event, formEl) {
            if (!formEl) return
            formEl.validate((valid) => {
                if (valid) {
                    this.$emit('join-game', this.ruleForm.roomID);
                    this.closeWindow();
                } else {
                    event.preventDefault();
                    alert('join the game failed!');
                }
            })
        }
    },

};
</script>

<style scoped>
