<template>
    <div>
        <textarea v-model="text" placeholder="ËæìÂÖ•ÊñáÊú¨" class="input-box" @keydown="handleKeydown"> </textarea>
        <button v-on:click="sendText" class="button">ÂèëÈÄÅ(Enter)</button>
    </div>
</template>

<script>
export default {
    setup() {
        const sleep = (ms) => {
            return new Promise(resolve => setTimeout(resolve, ms));
        };

        return {
            sleep,
        };
    },
    data() {
        return {
            text: '',
            counter : 1,
            coolDown : false
        };
    },
    props: {
        emojiMessage: String
    },
    watch: {
        emojiMessage(newVal) {
            if (newVal) {
                this.handleMessage();
            }
        }
    },
    methods: {
        sendText() {
            // ÊãíÁªùÂèëÈÄÅ
            if(this.text.length <= 0){
                return;
            }
            this.$emit('send-text', this.text);
            this.text = '';
        },
        handleKeydown(event) {
            if (this.coolDown) {
                return;
            }
            // Á¶ÅÊ≠¢ÂÖ∂‰ªñ‰∫ã‰ª∂
            this.coolDown = true;

            if (event.key === "Enter" && !event.ctrlKey) {
                event.preventDefault(); // Èò≤Ê≠¢ÈªòËÆ§ÁöÑÊç¢Ë°åË°å‰∏∫
                if(this.text.length > 0){
                    // console.log('curCounter: ', this.counter++);
                    this.sendText();
                }
                else{
                    alert('ÂèëÈÄÅÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫');
                }
            } else if (event.key === "Enter" && event.ctrlKey) {
                // ÊèíÂÖ•Êç¢Ë°åÁ¨¶
                this.text += "\n";
            }
            this.sleep(500);
            this.coolDown = false;
        },
        handleMessage() {
            this.text += this.emojiMessage;
            this.$emit('clearEmojiMessage');
        }
    }
};
</script>

<style scoped>
.input-box {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 98%;
    padding: 0vw 0.5vw;

    /* margin: 10px; */
    /*Ë°åÈó¥Ë∑ù */
    /* line-height: 2px; */

    font-family: Arial, sans-serif;
    /* Â≠ó‰Ωì */
    font-size: 1vw;
    /* Â≠ó‰ΩìÂ§ßÂ∞è */
    /* word-wrap: break-word;
    max-width: 100%; */
    /* ‰∏∫ÊåâÈíÆÁïôÂá∫Á©∫Èó¥ */
    /* margin: 10px; */
    height: 4vw;
    /* ÂàùÂßãÈ´òÂ∫¶ */
    border: none;
    /* ÂéªÈô§ËæπÊ°Ü */
    outline: none;
    /* ÂéªÈô§ÁÇπÂáªËæπÊ°Ü */
    background-color: #f5f5f5;
    resize: none;
    /* Á¶ÅÁî®ÊâãÂä®Ë∞ÉÊï¥Â§ßÂ∞è */
    overflow-y: auto;
    /* ÂÜÖÂÆπËøáÂ§öÊó∂ÊòæÁ§∫ÂûÇÁõ¥ÊªöÂä®Êù° */
    z-index: 1;
}

.button {
    border: none;
    position: absolute;

    right: 1%;
    bottom: 30%;
    padding: 0.8% 1.2%;
    background-color: #E9E9E9;
    color: #07C160;
    font-size: 1vw;
    margin: 0.3%;
    cursor: pointer;

    width: 10%;
    /* ÂèØÈÄâÔºöÊ∑ªÂä†Èò¥ÂΩ±Êù•Â¢ûÂä†ËßÜËßâÊïàÊûú */
    /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); */

    /* Á°Æ‰øùÊåâÈíÆ‰∏ç‰ºöË¢´Ë¶ÜÁõñÊéâ  */
    z-index: 1000;
    opacity: 1;
}

.button:hover {
    background-color: #494a4b;
    /* Èº†Ê†áÊÇ¨ÂÅúÊó∂ÁöÑËÉåÊôØÈ¢úËâ≤ */
}

.button:active {
    background-color: #004494;
    /* ÊåâÈíÆË¢´ÁÇπÂáªÊó∂ÁöÑËÉåÊôØÈ¢úËâ≤ */
    /* ÂèØÈÄâÔºöË∞ÉÊï¥Èò¥ÂΩ±Êù•Ê®°ÊãüÊåâÈíÆÊåâ‰∏ãÁöÑÊïàÊûú */
    box-shadow: 0 0.1% 0.3% rgba(0, 0, 0, 0.2);
}
</style>